___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100dvw;
  height: 100dvh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.carousel-container {
  width: 90%;
  height: 60%;

  .carousel-ol-list {
    width: 100%;
    height: 100%;
    position: relative;

    li:not(.active) {
      position: absolute;
      opacity: 0;
    }

    .active {
      position: relative;
      opacity: 1;
      transition: all 0.5s ease;
    }

    .previous,
    .next {
      position: absolute;
      width: 150px;
      height: 150px;
      background-color: transparent;
      background-repeat: no-repeat;
      background-size: 150px;
      border: none;
      top: 50%;
      transform: translateY(-50%);
      filter: invert(100%) sepia(0%) saturate(7491%) hue-rotate(353deg)
        brightness(103%) contrast(104%);
      z-index: 4;
    }

    .previous:hover {
      cursor: pointer;
    }
    .next:hover {
      cursor: pointer;
    }

    .next {
      right: 0;
      background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    }

    .previous {
      left: 0;
      opacity: 1;
      background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    }

    .carousel-indicators {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 10%;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;

      .indicator {
        width: 40px;
        background-color: white;
        opacity: 0.5;
        height: 3px;
      }

      .indicator:hover {
        cursor: pointer;
      }

      .indicator-active {
        background-color: white;
        opacity: 1;
      }
    }

    .carousel-item {
      list-style: none;
      height: 100%;
      width: 100%;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
